.lightbox {
    width: 100%;
    height: 100%;
    padding-top: 40 / $setFs + rem;
    padding-bottom: 40 / $setFs + rem;
    box-sizing: border-box;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    align-items: center;
    justify-content: center;
    display: none;
    opacity: 0;
    z-index: 999;

    @media screen and (max-width: 768px) {
        padding-top: 65 / $setFs + rem;
        padding-bottom: 65 / $setFs + rem;
    }

    &::-webkit-scrollbar {
        width: 8 / $setFs + rem;
    }

    &::-webkit-scrollbar-track {
        border-radius: 8 / $setFs + rem;
        background: rgba(255, 255, 255, 0.2);
    }

    &::-webkit-scrollbar-thumb {
        background: rgba(#58391a, 0.9);
    }
}


.lb_board {
    max-width: 960 / $setFs + rem;
    margin: auto;
    position: relative;

    .ind_paper {
        &:before {
            opacity: 1;
        }
    }

    .ind_title {
        margin-bottom: 15px;
    }

    .ind_title_en {
        margin-bottom: 30px;
    }

    .ind_subtitle {
        margin-bottom: 10px;
    }

    .ind_txt {
        letter-spacing: 1px;
        margin-bottom: 20px;
    }

    .ind_txt_en {
        margin-bottom: 15px;
    }

    .ind_pic {
        margin-bottom: 25px;
    }
}

.btnClose {
    width: 26 / $setFs + rem;
    height: 26 / $setFs + rem;
    border: 1 / $setFs + rem solid #fff;
    position: fixed;
    top: 20 / $setFs + rem;
    right: 20 / $setFs + rem;

    @media screen and (max-width: 480px) {
        right: 10 / $setFs + rem;
    }

    &:before,
    &:after {
        content: "";
        display: block;
        width: 100%;
        height: 2 / $setFs + rem;
        border-radius: 3 / $setFs + rem;
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -1 / $setFs + rem;
        background: #fff;
        transition: all 0.4s ease-out;
    }

    &:before {
        transform: rotate(-45deg);
    }

    &:after {
        transform: rotate(45deg);
    }

    &:hover {
        &:before {
            transform: rotate(45deg);
        }

        &:after {
            transform: rotate(-45deg);
        }
    }

    a {
        height: 100%;
        @include acc;
    }
}

.lb_btn {
    cursor: pointer;
}