@import "reset";
@import "variable";
@import "base";
@import "share";
@import "components/loading";
@import "components/lightbox";
@import "components/header";
@import "components/footer";
@import "components/btn_more";
@import "components/kv";
@import "components/list_el";

.permanent_kv {
    position: relative;
    overflow: hidden;

    @media screen and (max-width: 768px) {
        width: 150%;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
    }

    @media screen and (max-width: 480px) {
        width: 170%;
    }
}

.pm_kv_txt_box {
    width: 338 / 1903 * 100%;
    position: absolute;
    top: 126 / 644 * 100%;
    left: 412 / 1903 * 100%;
}

.pm_kv_txt {
    position: absolute;
    top: 0;
    left: 0;

    &.txt_1 {
        position: relative;
    }
}

.pm_kv_door {
    width: 20.18%;
    height: 100%;
    position: absolute;
    left: 50%;
    top: 0;
}

.pm_kv_peo {
    position: absolute;
}

.peo_1 {
    width: 96/384 * 100%;
    right: 50%;
    top: 35.9%;
    margin-right: 42.5%;
}

.peo_2 {
    width: 142/384 * 100%;
    right: 50%;
    top: 57.6%;
    margin-right: 36%;
}

.peo_3 {
    width: 105/384 * 100%;
    left: 50%;
    top: 30.2%;
    margin-left: 27.6%;
}

.peo_4 {
    width: 121/384 * 100%;
    left: 50%;
    top: 47%;
    margin-left: 41%;
}

.peo_5 {
    width: 124/384 * 100%;
    left: 50%;
    top: 63.7%;
    margin-left: 36.8%;
}

.pm_kv_door_cover {
    width: 49/384 * 100%;
    height: 15%;
    background-image: url("pm_kv_door_cover.jpg");
    background-size: 100% auto;
    background-repeat: repeat-y;
    background-position: left top;
    position: absolute;
    left: 50%;
    top: 40%;
    margin-left: 49.9%;
}

.description_sec {
    padding-top: 81 / $setFs + rem;
    padding-bottom: 82 / $setFs + rem;
    overflow: hidden;

    .container {
        max-width: 55.8rem;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            max-width: 65rem;
        }
    }

    .btn_row {
        margin-top: 50 / $setFs + rem;
    }
}

.shadow_2 {
    width: 31rem;
    position: absolute;
    bottom: -5 / $setFs + rem;
    left: 50%;
    margin-left: 26rem;
    opacity: .5;
    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        width: 26rem;
        margin-left: 21rem;
    }
}

.description_subTitle {
    font-size: 24 / $setFs + rem;
    font-family: $font2;
    line-height: (36/24);
    text-align: center;
    margin-top: 36 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (24 + 3) / $setFs + rem;
    }
}

.description_txt {
    font-size: 18 / $setFs + rem;
    text-align: center;
    line-height: (32/18);
    margin-top: 21 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (18 + 3) / $setFs + rem;
    }

    p {
        margin-bottom: 30 / $setFs + rem;
    }

    figcaption {
        font-size: 0.9em;
        padding-top: 8 / $setFs + rem;
        opacity: .5;
    }
}

.permanent_list_sec {
    padding-bottom: 40 / $setFs + rem;
}

.permanent_li {
    position: relative;
    margin-bottom: 40 / $setFs + rem;

    &:focus,
    &:hover {
        .permanent_bg {
            &:before {
                transform: scaleX(1);
            }
        }

        .permanent_row {
            color: #fff;
            transition-delay: 0.2s;
        }

        .permanent_img {
            transform: scale(1.05);
        }

        .btn_more {
            .fal {
                &:after {
                    background: #fff;
                }
            }
        }
    }

    @media screen and (max-width: 768px) {
        .permanent_bg {
            &:before {
                transform: scaleX(1);
            }
        }

        .permanent_row {
            color: #fff;
            transition-delay: 0.2s;
        }

        .permanent_img {
            transform: scale(1.05);
        }

        .btn_more {
            .fal {
                &:after {
                    background: #fff;
                }
            }
        }
    }

    &:nth-child(2n + 1) {
        .permanent_bg {
            left: 0;
            right: auto;

            @media screen and (max-width: 480px) {
                left: -10 / $setFs + rem;
            }

            &:before {
                transform-origin: right center;
            }
        }

        .permanent_caption {
            padding-left: 50 / $setFs + rem;

            @media screen and (max-width: 768px) {
                padding-left: 0;
                padding-right: 0;
            }
        }

        .permanent_title {
            text-align: right;

            @media screen and (max-width: 768px) {
                text-align: left;
            }
        }
    }

    &:nth-child(5n + 1) {
        .permanent_bg {
            &:before {
                background: #005577;
            }
        }
    }

    &:nth-child(5n + 2) {
        .permanent_bg {
            &:before {
                background: #837033;
            }
        }
    }

    &:nth-child(5n + 3) {
        .permanent_bg {
            &:before {
                background: #714068;
            }
        }
    }

    &:nth-child(5n + 3) {
        .permanent_bg {
            &:before {
                background: #14361c;
            }
        }
    }

    &:nth-child(5n + 3) {
        .permanent_bg {
            &:before {
                background: #47496f;
            }
        }
    }
}

.permanent_link {

    &:focus,
    &:hover {
        .permanent_bg {
            &:before {
                transform: scaleX(1);
            }
        }

        .permanent_row {
            color: #fff;
            transition-delay: 0.2s;
        }

        .permanent_img {
            transform: scale(1.05);
        }

        .btn_more {
            .fal {
                &:after {
                    background: #fff;
                }
            }
        }
    }

    @media screen and (max-width: 768px) {
        .permanent_bg {
            &:before {
                transform: scaleX(1);
            }
        }

        .permanent_row {
            color: #fff;
            transition-delay: 0.2s;
        }

        .permanent_img {
            transform: scale(1.05);
        }

        .btn_more {
            .fal {
                &:after {
                    background: #fff;
                }
            }
        }
    }
}

.permanent_bg {
    width: 100vw;
    height: 100%;
    background: #f8f8f8;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;

    @media screen and (max-width: 480px) {
        right: -10 / $setFs + rem;
    }

    &:before {
        content: "";
        width: 100%;
        height: 100%;
        background: #bc4336;
        position: absolute;
        top: 0;
        left: 0;
        transform: scaleX(0);
        transform-origin: left center;
        transition: transform 0.4s ease-out;
    }
}

.permanent_row {
    display: flex;
    align-items: center;
    padding: 30 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        padding: 35 / $setFs + rem;
    }

    @media screen and (max-width: 768px) {
        flex-wrap: wrap;
    }

    @media screen and (max-width: 480px) {
        padding: 30 / $setFs + rem 0;
    }

    .btn_more_row {
        margin-top: 10 / $setFs + rem;
    }
}

.permanent_pic {
    flex: 0 0 500 / $setFs + rem;
    overflow: hidden;
    position: relative;

    @media screen and (max-width: 768px) {
        flex: 0 0 100%;
        order: 1;
    }
}
.permanent_pic_caption{
    width: 100%;
    color: #fff;
    background: rgba(#000, .4);
    padding: 8px 15px;
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
    left: 0;
}
.permanent_img {
    @extend .img;
    transition: transform 0.4s ease-out;
}

.permanent_caption {
    flex: 1;
    padding-right: 50 / $setFs + rem;

    @media screen and (max-width: 768px) {
        flex: 0 0 100%;
        padding-top: 30 / $setFs + rem;
        padding-left: 0;
        padding-right: 0;
        order: 2;
    }

    @media screen and (max-width: 480px) {
        padding-top: 20 / $setFs + rem;
    }
}

.permanent_title {
    font-size: 40 / $setFs + rem;
    font-family: $font2;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (40 + 3) / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        font-size: 2.1rem;
    }
}

.permanent_txt {
    font-size: 18 / $setFs + rem;
    line-height: (32/18);
    margin-top: 11 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (18 + 3) / $setFs + rem;
    }
}

.arti_list {
    margin-top: 11 / $setFs + rem;
}

.arti_link {
    font-family: $font2;
    font-weight: bold;
    font-size: 18 / $setFs + rem;
    line-height: (32/18);
    display: inline;
    transition: none;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (18 + 3) / $setFs + rem;
    }

    &::before {
        content: '';
        width: 0%;
        height: 1px;
        background: #fff;
        position: absolute;
        bottom: -2px;
        left: 0;
        transition: width .3s ease-out;
    }

    &:hover {
        &::before {
            width: 100%;
        }
    }

    .fa-long-arrow-right {
        font-size: 16 / $setFs + rem;
        margin-right: 5 / $setFs + rem;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (16 + 3) / $setFs + rem;
        }
    }
}

.video_sec {
    padding-top: 81 / $setFs + rem;
    padding-bottom: 82 / $setFs + rem;
    background: #f8f8f8;

    .description_txt {
        width: 80%;
        margin-left: auto;
        margin-right: auto;
        padding-bottom: 30 / $setFs + rem;
        margin-top: 40 / $setFs + rem;

        @media screen and (max-width: 480px) {
            width: 100%;
        }
    }
}

.video_container {
    max-width: 90rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        max-width: 100rem;
    }
}

.video {
    position: relative;
}

.video_iframe {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.video_title {
    font-size: 40 / $setFs + rem;
    font-weight: normal;
    font-family: $font1;
    text-align: center;
    margin-bottom: 30 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (40 + 3) / $setFs + rem;
    }

    .en {
        font-size: .8em;
        text-transform: uppercase;
    }
}