.kv_sec {
    background-image: url('kv_bg.jpg');
}

.kv_container {
    max-width: 1600 / $setFs + rem;
}

.kv_row {
    display: flex;
    align-items: center;
    padding: 50 / $setFs + rem 0;

    @media screen and (max-width: 768px) {
        display: block;
        padding-top: 60 / $setFs + rem;
        padding-bottom: 100 / $setFs + rem;
    }
}

.kv_pic {
    flex: 0 0 660 / $setFs + rem;

    @media screen and (max-width: 768px) {
        width: 500 / $setFs + rem;
        margin: 0 auto;
    }

    @media screen and (max-width: 480px) {
        width: 80%;
        margin: 0 auto;
    }
}

.kv_caption {
    color: #fff;
    font-family: $font1;
    margin-left: 80 / $setFs + rem;

    @media screen and (max-width: 768px) {
        text-align: center;
        margin-left: 0;
        margin-top: 30 / $setFs + rem;
    }
}

.kv_title {
    font-size: 76 / $setFs + rem;
    font-family: $font2;
    line-height: 1.2;
    margin-bottom: 20 / $setFs + rem;

    @media screen and (max-width: 480px) {
        font-size: 50 / $setFs + rem;
        margin-bottom: 15 / $setFs + rem;
    }
}

.kv_txt {
    font-size: 23.5 / $setFs + rem;
    text-transform: uppercase;
    letter-spacing: 3 / $setFs + rem;
    text-indent: 3 / $setFs + rem;
}