.footer {
    color: #fff;
    background: #333;
    padding: 30 / $setFs + rem 0 0;
    border-top: 1 / $setFs + rem solid #a0a0a0;

    .container {
        max-width: none;
    }
}

.footer_row {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 14 / $setFs + rem;
    line-height: 2;

    @media screen and (max-width: 768px) {
        display: block;
    }

    &:first-child {
        margin-bottom: 14 / $setFs + rem;

        @media screen and (max-width: 768px) {
            margin-bottom: 30 / $setFs + rem;
        }
    }
}

.footer_logo_row {
    width: 350 / $setFs + rem;
    display: flex;
    align-items: center;
}

.footer_logo {
    flex: 0 0 45 / $setFs + rem;
}

.footer_logo_link {
    @include acc;
}

.footer_logo_title {
    font-size: 25 / $setFs + rem;
    font-weight: 700;
    font-family: $font2;
    letter-spacing: 3 / $setFs + rem;
    margin-left: 12 / $setFs + rem;
}

.footer_col {
    padding-left: 70 / $setFs + rem;

    @media screen and (max-width: 768px) {
        padding-left: 0;
        padding-bottom: 30 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        padding-bottom: 20 / $setFs + rem;
    }
}

.col_right {
    @media screen and (max-width: 768px) {
        padding-left: 70 / $setFs + rem;
        padding-bottom: 0;
    }

    @media screen and (max-width: 480px) {
        padding-left: 0;
    }
}

.footer_info_title {
    position: relative;

    &::after {
        content: "/";
        color: #666;
    }
}

.contact_row {
    display: flex;
    align-items: center;
    padding-left: 70 / $setFs + rem;

    @media screen and (max-width: 480px) {
        padding-left: 0;
        margin-top: 20 / $setFs + rem;
    }
}

.facebook {
    font-size: 16 / $setFs + rem;
    position: relative;
    margin-left: 20 / $setFs + rem;
}

.copyright {
    font-size: 14 / $setFs + rem;
    text-align: center;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding: 15 / $setFs + rem 0;
    margin-top: 35 / $setFs + rem;
}

.copyright_en {
    display: inline;
}