.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;

    @media screen and (max-width: 768px) {
        padding-top: 10 / $setFs + rem;
        padding-bottom: 12 / $setFs + rem;
    }

    &::before {
        content: "";
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.8);
        position: absolute;
        top: 0;
        left: 0;
    }

    .accesskey {
        top: 50%;
        transform: translateY(-50%);
    }
}

.header_container {
    max-width: none;
    display: flex;
}

.header_left {
    display: flex;
    align-items: center;
}

.header_right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    @media screen and (max-width: 768px) {
        flex: 1;
    }
}

.mainLogo_link {
    @include acc;
    white-space: nowrap;
    font-family: $font1;
    position: relative;
    display: flex;
}

.mainLogo_tw {
    font-size: 25 / $setFs + rem;
    font-weight: 700;
    font-family: $font2;
    letter-spacing: 3 / $setFs + rem;
    position: relative;
    left: -2 / $setFs + rem;
    bottom: -2 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (25 + 3) / $setFs + rem;
    }
}

.mainLogo_en {
    font-size: 12 / $setFs + rem;
    font-weight: 700;
    letter-spacing: 0;
    text-transform: uppercase;
    position: relative;
    transform: scale(.7);
    transform-origin: left bottom;
    margin-left: 7 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: 12 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        display: none;
    }
}

.hamburger {
    cursor: pointer;
    z-index: 99;
    display: none;

    @media screen and (max-width: 768px) {
        width: 40 / $setFs + rem;
        display: block;
        position: relative;
        margin-left: 30 / $setFs + rem;
    }

    a {
        padding: 20 / $setFs + rem 0;
    }

    span {
        display: block;
        width: 100%;
        height: 2 / $setFs + rem;
        background: $txtColor;
        border-radius: 4 / $setFs + rem;
        position: absolute;
        top: 50%;
        right: 0;
        margin-top: -1 / $setFs + rem;
        transition: all 0.6s;
    }

    &:before,
    &:after {
        content: "";
        display: block;
        width: 100%;
        height: 2 / $setFs + rem;
        background: $txtColor;
        border-radius: 4 / $setFs + rem;
        position: absolute;
        right: 0;
        top: 50%;
        margin-top: -1 / $setFs + rem;
        transition: all 0.4s;
    }

    $x: 8 / $setFs+rem;

    &:before {
        transform: translateY(-#{$x});
    }

    &:after {
        transform: translateY(#{$x});
        transform-origin: right center;
    }

    &.cross {

        span,
        &:before,
        &:after {
            background: $txtColor;
        }

        span {
            width: 75%;
            transition: all 0.4s ease-out;
        }

        &:before,
        &:after {
            transition: all 0.4s ease-out;
        }

        &:before {
            transform: translateY(#{$x});
        }

        &:after {
            transform: translateY(-#{$x}) scaleX(0.5);
        }
    }
}

.nav {
    @media screen and (max-width: 768px) {
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.95);
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 5;
    }
}

.nav_container {
    @media screen and (max-width: 768px) {
        width: 100%;
        max-height: 100%;
        padding: 50 / $setFs + rem 0;
        box-sizing: border-box;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }
}

.nav_list {
    display: flex;
    justify-content: center;
    font-family: $font1;
    margin-right: -23 / $setFs + rem;

    @media screen and (max-width: 768px) {
        display: block;
        width: 100%;
        margin-right: 0;
    }
}

.nav_li {
    position: relative;

    @media screen and (min-width: 769px) {
        &:hover {
            .subnav_box {
                visibility: visible;
                opacity: 1;
                transform: translateX(-50%) translateY(0);
            }
        }
    }
}

.nav_link {
    font-size: 17 / $setFs + rem;
    font-weight: 500;
    text-align: center;
    padding: 31 / $setFs + rem 23 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (17 + 3) / $setFs + rem;
    }

    @media screen and (max-width: 768px) {
        padding: 20 / $setFs + rem 0;
    }

    @media screen and (max-width: 480px) {
        font-size: 20 / $setFs + rem;
    }

    &.active,
    &:focus,
    &:hover {
        @media screen and (min-width: 769px) {
            background: $color2;

            // color: #fff;
            .subnav_box,
            &+.subnav_box {
                visibility: visible;
                opacity: 1;
                transform: translateX(-50%) translateY(0);
            }
        }
    }
}

.header_lang {
    margin-left: 5 / $setFs + rem;
}

.m_header_lang {
    position: relative;

    @media screen and (min-width: 769px) {
        display: none;
    }

    .fa-globe {
        font-size: 1.4rem;
        position: relative;
        bottom: -3 / $setFs + rem;
    }

    .subnav_box {
        padding: 8 / $setFs + rem 0;
        display: none;
        transform: translateX(-50%);
        position: absolute;
        top: 100%;
        left: 50%;
    }

    .subnav_list {
        background: rgba(255, 255, 255, 0.9);
        box-shadow: 2 / $setFs + rem 3 / $setFs + rem 7 / $setFs + rem rgba(0, 0, 0, 0.2);
    }

    .subnav_link {
        padding: 8 / $setFs + rem 40 / $setFs + rem;
    }
}

.subnav_box {
    visibility: hidden;
    opacity: 0;
    transform: translateX(-50%) translateY(15px);
    position: absolute;
    top: 100%;
    left: 50%;
    transition: all 0.2s ease-out;

    @media screen and (max-width: 768px) {
        position: relative;
        top: 0;
        left: 0;
        transform: none;
        visibility: visible;
        opacity: 1;
        transition: none;
    }
}

.subnav_list {
    padding: 15 / $setFs + rem 0;
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 2 / $setFs + rem 3 / $setFs + rem 7 / $setFs + rem rgba(0, 0, 0, 0.2);

    @media screen and (max-width: 768px) {
        box-shadow: none;
        background: rgba(0, 0, 0, 0.05);
    }
}

.subnav_link {
    color: $txtColor;
    text-align: center;
    padding: 15 / $setFs + rem 50 / $setFs + rem;
    white-space: nowrap;
    transition: background 0.2s;

    @media screen and (min-width: 769px) {

        &:hover,
        &:focus {
            background: $color;
            color: #fff;
        }
    }

    @media screen and (max-width: 768px) {
        padding: 12 / $setFs + rem 0;
    }

    @media screen and (max-width: 480px) {
        font-size: 19 / $setFs + rem;
    }
}