@mixin acc_before {
    @media screen and (min-width: 769px) {
        &:before {
            content: "";
            width: calc(100% + 4px);
            height: calc(100% + 4px);
            border: 2px dotted $color;
            border-radius: 3 / $setFs + rem;
            box-sizing: border-box;
            position: absolute;
            top: -2px;
            left: -2px;
            z-index: 5;
            opacity: 0;
            transition: opacity 0.1s ease-out;
        }

        &:focus {
            &:before {
                opacity: 1;
            }
        }
    }
}

@mixin acc_w {
    @include acc;
    @include _w;
}

@mixin acc_before_w {
    @include acc_before;
    @include _w;
}

.hide {
    display: none;
}

body .pc {
    @media screen and (max-width: 768px) {
        display: none;
    }
}

body .pad {
    display: none;

    @media screen and (max-width: 768px) {
        display: block;
    }

    @media screen and (max-width: 580px) {
        display: none;
    }
}

body .mobile {
    display: none;

    @media screen and (max-width: 580px) {
        display: block;
    }
}

.clear {
    &:before {
        content: "";
        display: table;
    }

    &:after {
        @extend :before;
        clear: both;
    }
}

.c {
    clear: both;
}

@mixin clear {
    clear: both;
}

@mixin clearfix {
    &:before {
        content: "";
        display: table;
    }

    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

img {
    width: 100%;
    height: auto;
    display: block;
}

iframe {
    width: 100%;
    height: auto;
}

table {
    width: 100%;

    th,
    td {
        vertical-align: middle;
    }
}

.table {
    width: 100%;
    display: table;
    table-layout: fixed;
}

.tr {
    display: table-row;
}

.td {
    display: table-cell;
    vertical-align: middle;
    position: relative;
}

.th {
    display: table-cell;
    vertical-align: middle;
    position: relative;
}

.inblock {
    font-size: 0;
    letter-spacing: 0;

    >* {
        display: inline-block;
        vertical-align: middle;
        letter-spacing: 1 / $setFs + rem;
    }
}

@mixin inblock($spacing: 0) {
    font-size: 0;
    letter-spacing: 0;

    >* {
        display: inline-block;
        vertical-align: top;
        letter-spacing: $spacing / $setFs + rem;
    }
}