.back_sec {
    padding: 30 / $setFs + rem 0 80 / $setFs + rem;
}

.back_link,
.btn_more_row {
    display: flex;
}

.back_link {
    @include acc;
}

.btn_more_row {
    justify-content: flex-end;
}

.back_arrow,
.btn_more {
    width: 52 / $setFs + rem;
    height: 52 / $setFs + rem;
    background-image: url("more_circle.png");
    background-size: contain;
    background-position: right top;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .fal {
        font-size: 37 / $setFs + rem;
        position: relative;
        right: -2px;
        top: 1px;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (37 + 3) / $setFs + rem;
        }

        &:after {
            content: "";
            width: 0;
            height: 1 / $setFs + rem;
            background: $txtColor;
            position: absolute;
            top: 50%;
            right: 50%;
            margin-top: -2 / $setFs + rem;
            margin-right: -2 / $setFs + rem;
            transition: width 0.3s linear;
        }
    }
}

.back_arrow {
    background-image: url("back_circle.png");

    .fal {
        &:after {
            right: auto;
            left: 50%;
            margin-right: 0;
            margin-left: -2 / $setFs + rem;
        }
    }
}

.back_txt,
.btn_more_txt {
    font-size: $setFs;
    white-space: nowrap;
    position: absolute;
    right: 72%;
    top: 50%;
    transform: translateY(-50%);
    transition: transform 0.6s cubic-bezier(0.25, 1, 0.5, 1);
}

.back_txt {
    right: auto;
    left: 72%;
}

a {
    &:hover {
        .btn_more_txt {
            $x: -60 / $setFs + rem;
            transform: translateY(-50%) translateX(#{$x});
        }

        .back_txt {
            $x: 60 / $setFs + rem;
            transform: translateY(-50%) translateX(#{$x});
        }

        .fal {
            &:after {
                width: 65 / $setFs + rem;
                transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
            }
        }
    }
}