.item_list_sec {
    padding-top: 50 / $setFs + rem;
    padding-bottom: 82 / $setFs + rem;

    .pagination_list {
        @media screen and (max-width: 768px) {
            margin-top: 30 / $setFs + rem;
        }
    }
}

.item_list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 40 / $setFs + rem;
    margin-left: -20 / $setFs + rem;
    margin-right: -20 / $setFs + rem;

    @media screen and (max-width: 768px) {
        margin-left: -10 / $setFs + rem;
        margin-right: -10 / $setFs + rem;
    }
}

.item_li {
    flex: 0 0 33.333333%;
    padding: 0 20 / $setFs + rem;
    box-sizing: border-box;
    margin-bottom: 60 / $setFs + rem;

    @media screen and (max-width: 768px) {
        flex: 0 0 50%;
        padding: 0 10 / $setFs + rem;
        margin-bottom: 50 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        flex: 0 0 100%;
        margin-bottom: 50 / $setFs + rem;
    }
}

.item_link {
    &:hover {
        opacity: .7;
    }
}

.item_pic {
    position: relative;

    @media screen and (max-width: 480px) {
        margin-left: -10 / $setFs + rem;
        margin-right: -10 / $setFs + rem;
    }
}

.item_caption {
    padding-top: 20 / $setFs + rem;
}

.item_tag {
    @extend .tag;
    padding: 0 10 / $setFs + rem;
    line-height: 26 / $setFs + rem;
}

.item_title {
    @include word_limit;
    font-family: $font2;
    font-size: 24 / $setFs + rem;
    margin-top: 12 / $setFs + rem;

    @media screen and (max-width: 480px) {
        font-size: 20 / $setFs + rem;
    }
}

.item_txt {
    @include word_limit;
    line-height: (32/18);
    margin-top: 15 / $setFs + rem;

    @media screen and (max-width: 768px) {
        margin-top: 5 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        margin-top: 0;
    }
}