.accesskey {
    font-size: 16 / $setFs + rem;
    position: absolute;
    right: 100%;
    top: 0;
    margin-right: 10 / $setFs + rem;
    z-index: 1;
    @include acc;

    @media screen and (max-width: 768px) {
        display: none;
    }
}

.sr-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1 / $setFs + rem;
    margin: -1 / $setFs + rem;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1 / $setFs + rem;
}

.pb50 {
    padding-bottom: 50 / $setFs + rem;
}

.title {
    @include word_limit(2);
    font-family: $font2;
    font-weight: 600;
}

.mainTitle_row {
    &.center {
        text-align: center;

        &.line {
            position: relative;

            .zh {
                padding-bottom: 14 / $setFs + rem;

                &:after {
                    content: "";
                    width: 96 / $setFs + rem;
                    height: 3 / $setFs + rem;
                    background: #333;
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
        }

        &.dash {
            .zh {
                position: relative;
                margin-top: 0;

                &:after,
                &:before {
                    content: "";
                    width: calc(50% - 2.8em);
                    height: 1 / $setFs + rem;
                    background: $txtColor;
                    position: absolute;
                    top: 50%;
                    margin-top: 1 / $setFs + rem;
                }

                &:before {
                    right: 50%;
                    margin-right: 8em;
                }

                &:after {
                    left: 50%;
                    margin-left: 8em;
                }
            }
        }
    }

    &.left {
        .mainTitle {
            padding-left: 30 / $setFs + rem;
            margin-left: 80 / $setFs + rem;
            margin-bottom: 36 / $setFs + rem;
            position: relative;

            @media screen and (max-width: 768px) {
                margin-left: 20 / $setFs + rem;
            }

            @media screen and (max-width: 480px) {
                margin-left: 10 / $setFs + rem;
            }

            &::before {
                content: "";
                width: 3 / $setFs + rem;
                height: 200 / $setFs + rem;
                background: $txtColor;
                position: absolute;
                left: 0;
                top: 5 / $setFs + rem;
            }

            .en {
                @media screen and (max-width: 768px) {
                    font-size: 50 / $setFs + rem;
                    letter-spacing: 1 / $setFs + rem;
                }

                @media screen and (max-width: 480px) {
                    font-size: 46 / $setFs + rem;
                }
            }
        }

        &.noline {
            .mainTitle {
                padding-left: 0;
                margin-left: 0;
                margin-bottom: 15 / $setFs + rem;

                &::before {
                    display: none;
                }

                .zh {
                    margin-top: 0;
                }
            }
        }
    }
}

.mainTitle {
    font-family: $font2;

    .en {
        font-size: 54 / $setFs + rem;
        font-weight: normal;
        letter-spacing: 2 / $setFs + rem;
        display: block;
        line-height: 1.4;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (54 + 3) / $setFs + rem;
        }

        @media screen and (max-width: 480px) {
            font-size: 48 / $setFs + rem;
        }
    }

    .zh {
        font-size: 2.18rem;
        font-weight: 700;
        display: block;
        margin-top: 10 / $setFs + rem;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: 2.4rem;
        }

        @media screen and (max-width: 480px) {
            font-size: 1.8rem;
        }
    }
}

.img {
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}

.tracker {
    width: 15 / $setFs + rem;
    height: 15 / $setFs + rem;
    border-radius: 15 / $setFs + rem;
    pointer-events: none;
    background: #dd472f;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 49;
    transition: opacity 0.3s ease-out;

    @media screen and (max-width: 768px) {
        display: none;
    }

    &:before {
        content: "";
        width: 0;
        height: 0;
        pointer-events: none;
        background-image: url("tracker.png");
        background-size: contain;
        background-position: center center;
        background-repeat: no-repeat;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: all 0.3s ease-out;
    }

    &.hover {
        background: transparent;

        &:before {
            width: 83 / $setFs + rem;
            height: 70 / $setFs + rem;
        }
    }
}

.hover {
    @include acc;
}

.btn_row {
    display: flex;
    justify-content: center;
}

.btn {
    width: 224 / $setFs + rem;
}

.btn_link {
    font-size: 17 / $setFs + rem;
    color: #fff;
    font-family: $font2;
    font-weight: 200;
    letter-spacing: 5 / $setFs + rem;
    text-align: center;
    line-height: 60 / $setFs + rem;
    background: $txtColor;
    appearance: none;
    position: relative;
    z-index: 1;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (17 + 3) / $setFs + rem;
    }

