    //     height: 0;
    //     background: $color;
    //     position: absolute;
    //     bottom: 0;
    //     left: 0;
    //     z-index: -1;
    //     transition: height 0.1s ease-out;
    // }
    &:hover {
        color: #fff;
        //     &:after {
        //         height: 100%;
        //     }
    }
}

.page_top {
    background: $color2;
    padding-top: 55 / $setFs + rem;
}

.page_banner_img {
    height: 600 / $setFs + rem;
    object-fit: cover;

    @media screen and (max-width: 768px) {
        height: 50vw;
    }

    @media screen and (max-width: 480px) {
        height: 76vw;
    }
}

.swiper-button-prev,
.swiper-button-next {
    width: 53 / $setFs + rem;
    height: 53 / $setFs + rem;
    background-image: url("swiper_btn.png");
    background-size: contain;
    background-position: center center;
    transition: background-image 0.2s ease-out;

    &:after {
        font-size: 0;
    }

    &:focus {
        @include acc;
    }

    &:hover {
        background-image: url("swiper_btn_hover.png");
    }
}

.swiper-button-next {
    transform: scaleX(-1);
}

.tag {
    padding: 0 24 / $setFs + rem;
    line-height: 35 / $setFs + rem;
    background: #dbdbdb;
    border-radius: 24 / $setFs + rem;
    display: inline-block;
}

.pic {
    position: relative;
}

.img {
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}